.img-wrapper {
    position: relative;
    width: 80px;
    height: 80px;
    display: inline-block;
    margin-left: 10px;
}
.img-wrapper .close-btn {
    display: none;
    position: absolute;
    top: -8px;
    right: -8px;
    width: 18px;
    height: 18px;
    line-height: 18px;
    font-size: 16px;
    border-radius: 9px;
    text-align: center;
    cursor: pointer;
}
.img-wrapper:hover .close-btn {
    display: block;
    /* color: #fff;
    background: rgba(255, 255, 255, 0.6); */
    color: #fff;
    background-color: #333;
}
