/*  */
.new-price-input .zent-form__controls {
    
  }
  .new-price-input, .old-price-input {
      display: inline-block;
  }
  .new-price-input .zent-input {
      width: 30px;
  }
  .new-price-input .zent-form__help-desc {
      float: left;
      width: 100px;
      font-size: 12px;
  }
  .old-price-input .zent-form__control-label {
      display: none !important;
  }
  .old-price-input .zent-input{
      width: 30px;
  }
  /*  */
  .card-input .zent-form__control-label {
      display: none !important;
  }
  .card-input .zent-form__controls {
      margin-left: 0 !important;
  }

/* input */
.cart-cell .cart-name-input {
    width: 70px;
}
.cart-cell .cart-detail-input {
    width: 100px;
}
.cart-cell .zent-number-input-wrapper .zent-input {
    width: 0px;
}


