.pic-wrapper li, .pic-wrapper .all-pic li {
    width: 50px;
    display: inline-block;
    margin: 0 0 5px 5px;
}
.pic-wrapper .more-pic {
    display: inline-block;
    padding-left: 1px;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
}
.pic-wrapper .all-pic {
    width: 110px;
}


