.date-li {
    padding-bottom: 45px;
}
.date-li .time-left, .schedule .time-right {
    position: relative;
    display: inline-block;
}
/*  */
.date-li .time-edit {
    color: blue;
    font-weight: 700;
}
.date-li .time-mid {
    margin: 0 10px;
}
.date-li .time-show {
    font-size: 12px;
    cursor: pointer;
}
/* timepicker */
.date-li .time-picker {
    position: absolute;
    top: 20px;
    left: 0;
}
.date-li .date-wrapper {
    float: left;
}
/* timepicker */
.ant-time-picker-panel-select {
    width: 50px;
}
/*  */
.date-li .delete-btn, .date-li .time-btn {
    cursor: pointer;
    font-size: 12px;
    color: red;
    margin-left: 10px;
}
.date-li .delete-btn-hidden {
    display: none;
}
.date-li .time-btn {
    display: inline-block;
    color: green;
}


